import React, { useState, useEffect, useReducer } from 'react';
import bridge from '@vkontakte/vk-bridge';
import requestMethod from './helpers/requestMethod';
import axios from 'axios';
import config from './config';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import ActionSheet from '@vkontakte/vkui/dist/components/ActionSheet/ActionSheet';
import ActionSheetItem from '@vkontakte/vkui/dist/components/ActionSheetItem/ActionSheetItem';
import Icon28MusicMicOutline from '@vkontakte/icons/dist/28/music_mic_outline';
import Icon28SmileOutline from '@vkontakte/icons/dist/28/smile_outline';
import Icon28TagOutline from '@vkontakte/icons/dist/28/tag_outline';
import Icon28Profile from '@vkontakte/icons/dist/28/profile';

import Home from './panels/Home';

const App = () => {
	const navigator = window.navigator.onLine;
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [activeTab, setActiveTab] = useState('home');
	const [networkStatus, setNetworkStatus] = useState(false);
	const [popout, setPopout] = useState();
	const [getPlatform, setGetPlatform] = useState("");
	const [paymentStatus, setPaymentStatus] = useState(false);
	const [inventory, setInventory] = useState(false);
	const [durkaCoins, setDurkaCoins] = useState(0);
	const [shisoids, setShisoids] = useState(0);
	const [amount, setAmount] = useState();
	const [targetId, setTargetId] = useState();
	let targetIdShop = 0;
	const [dataUsers, setDataUsers] = useState([]);
	const [dataShop, setDataShop] = useState([
		{
			id: 1,
			name: "Blue пилюля	",
			price: 50,
			icon: <Icon28MusicMicOutline fill="blue" />
		},
		{
			id: 2,
			name: "Red пилюля",
			price: 500,
			icon: <Icon28MusicMicOutline fill="red" />
		},
		{
			id: 3,
			name: "Gold пилюля",
			price: 5000,
			icon: <Icon28MusicMicOutline fill="orange" />
		},
		{
			id: 4,
			name: "Отпуск",
			price: 50,
			icon: <Icon28TagOutline fill="black" />
		},
		{
			id: 5,
			name: "Смирительная рубашка",
			price: 50,
			icon: <Icon28SmileOutline fill="black" />
		},
		{
			id: 6,
			name: "Одиночная камера",
			price: 500,
			icon: <Icon28Profile fill="black" />
		}
	]);

	const [dataInventory, setDataInventory] = useState([
		{
			id: 1,
			name: "Blue пилюля",
			price: 50,
			icon: <Icon28MusicMicOutline fill="blue" />
		}
	]);

	const updateConnection = (navigator) => {
		if (!navigator) {
			setNetworkStatus(true);
		} else {
			setNetworkStatus(false);
		}
	};

	useEffect(() => {
		if (!navigator) {
			setNetworkStatus(true);
			console.log("checkNetworkStatus", navigator);
		} else {
			setNetworkStatus(false);
			console.log("checkNetworkStatus", navigator);
		}
		console.log("dataUsers:", dataUsers);
	}, [navigator]);

	useEffect(() => {

		localStorage.setItem(
			'search',
			document.location.search
		);

		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});

		axios.post(`${requestMethod}/getUser`, {
			search: localStorage.getItem('search')
		}).then((res) => {
			setDurkaCoins(res.data.message.durkaCoins);
			setShisoids(res.data.message.shisoids.length);
		});

		bridge.send('VKWebAppGetUserInfo').then(async (user) => {
			setUser(user);
		});

		getUsers();
		getHash();

		bridge.sendPromise('VKWebAppGetClientVersion',
		).then(res => {
			// console.log("ВЕРСИЯ ПРИЛОЖЕНИЯ:", res);
			if (res.platform === "ios") {
				setGetPlatform("ios");
			}
		}).catch(error => {
			console.log(error);
		});
	}, []);


	function story() {
		let url = `https://vk.com/app7351258#${fetchedUser.id}`;
		bridge.sendPromise('VKWebAppShowStoryBox', {
			"background_type": "image",
			"url": "https://sun1-89.userapi.com/HoZMa0ZNNFflt4SDnoomx0bNiCmRV-bOhbz3qw/hg7IHG1XIvg.jpg",
			"attachment": {
				"text": "go_to",
				"type": "url",
				"url": url
			}
		}).then(res => {
			console.log("story res:", res); // работает
		}).catch(error => {
			console.log("story error:", error);
		});
	}

	function addGroup() {
		bridge.send("VKWebAppJoinGroup", { "group_id": 192779261 });
		console.log("вызвали функцию.:", addGroup);
	}

	function inventoryOpen() {
		{
			inventory
				?
				setInventory(false)
				:
				setInventory(true)
		}

	}

	function tabShop() {
		setActiveTab('shop');
	}

	function tabHome() {
		setActiveTab('home');
	}

	function tabTop() {
		setActiveTab('top');
	}




	function onChangeHandlerId(e) {
		const value = e.target.value.trim();
		setTargetId(value);
	}

	function onChangeHandlerAmount(e) {
		const value = e.target.value.trim();
		setAmount(value);
	}


	function transfer() {

		console.log("ID User :", targetId);
		console.log("User amount :", amount);

		axios.post(`${requestMethod}/sendDurkaCoins`, {
			search: localStorage.getItem('search'),
			targetId,
			amount
		}).then((res) => {
			setDurkaCoins(durkaCoins - amount);
			setTargetId("");
			setAmount("");
			getUsers();
			console.log("transfer res", res);
		}).catch((err) => {
			console.log("transfer ERROR", err);
		});
		
	}


	function pay(e) {
		// const targetIdShop = e.currentTarget.id;
		// console.log('VALUE targetIdShop',targetIdShop);
		const priceItem = dataShop[targetIdShop - 1].price * 0.2;

		bridge.sendPromise('VKWebAppOpenPayForm', {
			"app_id": 7351258,
			"action": "pay-to-group",
			"params": {
				"amount": priceItem,
				"group_id": 192779261    // моя 192779261
			}
		}).then(async (res) => {
			if (res.status || res.result.status) {
				try {
					const response = (await axios.get(
						requestMethod(
							`checkTransaction?userId=${fetchedUser.id}`
						)
					)).data;

					if (response.status === 'success') {
						setPaymentStatus(true);
						console.log('ОПЛАТА ПРОШЛА 1');

						// targetList1(); // вызвать функцию которая меняет Роль пользователя
					} else {
                        /**
                         * Оплата не прошла
                         * или ее не было
                         */
					}
				} catch (error) {
					// Браузерная ошибка
				}
			}
		}).catch(error => {
			console.log(error);
		});
	}

	document.qiwiPay = () => {
		console.log('Вызвана функция qiwiPay');
		const priceItem = dataShop[targetIdShop - 1].price * 0.2;
		window.QiwiCheckout.createInvoice({
			publicKey: '48e7qUxn9T7RyYE1MVZswX1FRSbE6iyCj2gCRwwF3Dnh5XrasNTx3BGPiMsyXQFNKQhvukniQG8RTVhYm3iPuaYVi5RBePrfT1bReScJaiVSaSRm3C2n7ojMWhn6FwadkMSHfVa9GEQaH6vxb8khX6TUwvZUBhqfgBNSx5zTnEf5LxBvmue1CEXUZkF1Z',
			amount: priceItem,
			phone: '79625494201',
		}).then((data) => {
			alert(data)
			console.log(
				'QIWI RESOLVE',
				data
			);
			// document.querySelector('#root').style="";
		})
			.catch((error) => {
				console.log(
					'QIWI ERROR:',
					error
				);
				// document.querySelector('#root').style="";
			});
	}


	function share(e) {
		//  ДОБАВЛЕНИЕ РЕПОСТА НА СТЕНУ ПОЛЬЗОВАТЕЛЯ
		e.preventDefault();
		let url = `https://vk.com/app7351258#${fetchedUser.id}`;
		const photo = `photo-192779261_457239018,https://vk.com/app7351258#${fetchedUser.id}`;

		bridge.send("VKWebAppShowWallPostBox", {
			"message": `А ты уже был в ДУРКЕ? Приложение - ${url}`,
			"attachments": photo,
		});
	}


	function getUsers() {
		axios.post(`${requestMethod}/getUsers`, {
			search: localStorage.getItem('search')
		})
			.then((res) => {
				dataUsers.push(res.data.message);
				setDataUsers(res.data.message);
				console.log("getUsers", res);
			})
			.catch((err) => {
				console.log(err);
			});
	}

	// Копирование в буфер
	function copyLink() {
		// location.hash = "part5";
		const link = `https://vk.com/app7351258#${fetchedUser.id}`;

		window.navigator.clipboard.writeText(link).then(function () {
			console.log("successfully set");
		}, function () {
			console.log("write failed");
		});
	}

	function openBase() {
		console.log("вызвали функцию", openBase);
		setPopout(<>
			<ActionSheet onClose={() => setPopout(null)}>
				<ActionSheetItem autoclose onClick={copyLink} >
					Скопировать ссылку
                        </ActionSheetItem>
				<ActionSheetItem autoclose onClick={story}>
					Поделиться в истории
                        </ActionSheetItem>
				<ActionSheetItem autoclose onClick={share}>
					Поделиться на стене
                        </ActionSheetItem>
			</ActionSheet>
		</>)
			;
	}



	function getHash() {
		const { hash } = window.location;

		if (
			!hash
			|| Number.isNaN(+(hash.replace('#', '')))
		) {
			return;
		}

		const referrerId = Number(hash.replace('#', ''));

		axios.post(`${requestMethod}/addShisoid`, {
			referrerId: referrerId,
			search: localStorage.getItem('search')
		}).then((res) => {
			console.log('RESSS', res.data);
			if (res.data.status === 'success') {
				// То заебись, всё успешно добавилось

				return;
			}

			// Иначе нихуя не успешно
		}).catch((error) => {
			console.log('addshERROR', error);
		});
	}

	function openPay(e) {
		// setTargetId(e.currentTarget.id);
		targetIdShop = e.currentTarget.id;
		console.log('VALUE targetIdShop', targetIdShop);
		const priceItem = dataShop[targetIdShop - 1].price * 0.2;
		console.log('priceItem1', priceItem);

		setPopout(<>
			<ActionSheet onClose={() => setPopout(null)}>
				<ActionSheetItem autoclose>
					Оплатить DurkaCoin
                </ActionSheetItem>
				<ActionSheetItem autoclose onClick={document.qiwiPay} >
					Оплатить через QIWI
                </ActionSheetItem>
				<ActionSheetItem autoclose onClick={pay}>
					Оплатить через VK Pay
                </ActionSheetItem>
			</ActionSheet>
		</>)
			;
	}

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<View
			activePanel={activePanel}
			popout={popout} >
			<Home id='home'
				fetchedUser={fetchedUser}
				go={go}
				tabShop={tabShop}
				tabHome={tabHome}
				tabTop={tabTop}
				activeTab={activeTab}
				dataUsers={dataUsers}
				getPlatform={getPlatform}
				story={story}
				addGroup={addGroup}
				openBase={openBase}
				dataShop={dataShop}
				openPay={openPay}
				inventoryOpen={inventoryOpen}
				inventory={inventory}
				dataInventory={dataInventory}
				transfer={transfer}
				targetId={targetId}
				targetIdShop={targetIdShop}
				onChangeHandlerId={onChangeHandlerId}
				onChangeHandlerAmount={onChangeHandlerAmount}
				shisoids={shisoids}
				durkaCoins={durkaCoins}
				amount={amount}
			/>

		</View>
	);
}

export default App;

