import React from 'react';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Link from '@vkontakte/vkui/dist/components/Link/Link';
import styles from "../styles/custom-styles.sass";
// import Icon28MusicMicOutline from '@vkontakte/icons/dist/28/music_mic_outline';
// import Icon28GhostOutline from '@vkontakte/icons/dist/28/ghost_outline';


const ListUsersExpect = ({ id, name, color, price, icon, openPay }) => {
    // const urlUser = 'https://vk.com/id' + idVk;

    return (
        <Div className='card'>
            <Cell
                className='myItem'
                before={icon}
                description={<Link 
                    id={id}
                    onClick={openPay}
                    mode="tertiary"
                >
                    купить
                </Link>}
            >
                {name + " - " + price + " " + "DCoin"}
            </Cell>
            {/* <Link onClick={openPay}>
                Купить
            </Link> */}
            
        </Div>
    );
};

export default ListUsersExpect;

